import React from 'react';
import { Box } from '@mui/material';

const AcceptedWords = ({ words }) => {
  return (
    <Box sx={{
      position: 'fixed',
      bottom: 0,
      left: 0,
      right: 0,
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
      backgroundColor: 'black', // Or any other color you prefer
      color: 'white',
      padding: '10px',
      maxWidth: '100%', // Ensures it doesn't extend beyond the viewport
      overflowWrap: 'break-word', // Allows breaking words onto the next line
      wordWrap: 'break-word', // For older browsers
      whiteSpace: 'normal', // Allows wrapping to multiple lines
      overflow: 'hidden', // Prevents extending past max width
      textAlign: 'center' // Centers the text horizontally
    }}>
      {words.map((word, index) => (
        <Box key={index} sx={{ margin: '0 10px' }}>
          {word}
        </Box>
      ))}
    </Box>
  );
};

export default AcceptedWords;
